export class T extends Component {

	constructor(props) {
    super(props);

    this.changeSource = this.changeSource.bind(this)
  }

	state = {
		sourceIndex : 0,
		svgSet : [ t1, t2, t3 ]
	}

	

	componentDidMount () {

	 this.setState({char: this.props.char});
	 // var prevSet = this.state.svgSet;
	 // var newSet = this.shuffle(prevSet);
	 
	 // console.log('prev : ',prevSet)
	 // console.log('new : ',newSet)

	 // this.setState({svgSet: newSet});

	 if (this.props.loop ){

	 	this.timeout = setTimeout (
	 		this.changeSource,
	 		this.props.animDuration * 10
	 		)
	 }

	}

	componentWillMount () {
	  // clearInterval(this.interval);
	  if (this.timeout) clearTimeout(this.timeout)

	  // pull random character set from this.props.char 
	  // feed state 	
	}
	shuffle = (a) => {
    for (let i = a.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [a[i], a[j]] = [a[j], a[i]];
    }
    return a;
	}

	changeSource () {
		// let newSet = this.shuffle(this.state.svgSet);
		// this.setState({svgSet: newSet});
		this.setState(function({sourceIndex}) {
			const nextSourceIndex = ++ sourceIndex % this.state.svgSet.length
			return { sourceIndex : nextSourceIndex}
		}, function(){
			this.timeout = setTimeout(
				this.changeSource,
				this.props.animDuration * 100
			)
		})
	}
				// <img src={this.state.charSet[this.state.sourceIndex]} alt={"char-"+this.props.char} />
				// {this.state.charSet[this.state.sourceIndex]}

	render() {
		// console.log(this.state.charSet[this.state.sourceIndex])
		return (
			<div className="char-wrapper">
				<img src={this.state.svgSet[this.state.sourceIndex]} alt="T" className="char-svg" />
			</div>
		);
	}
}

export default T;