import React, { Component } from 'react';

import r1 from './src/01.svg';
import r2 from './src/02.svg';
import r3 from './src/03.svg';
import r4 from './src/04.svg';
// import h5 from './src/05.png';
// import h6 from './src/06.png';
// import h7 from './src/07.png';
// import h8 from './src/08.png';
// import h9 from './src/09.png';
// import h10 from './src/10.png';

export class R extends Component {

	constructor(props) {
    super(props);

    this.changeSource = this.changeSource.bind(this)
  }

	state = {
		sourceIndex : 0,
		svgSet : [ r1,r2,r3,r4 ]
	}

	

	componentDidMount () {

	 this.setState({char: this.props.char});



	 if (this.props.loop ){

	 	this.timeout = setTimeout (
	 		this.changeSource,
	 		this.props.animDuration * 10
	 		)
	 }

	}

	componentWillMount () {
	  // clearInterval(this.interval);
	  if (this.timeout) clearTimeout(this.timeout)

	  // pull random character set from this.props.char 
	  // feed state 	
	}

	changeSource () {
		this.setState(function({sourceIndex}) {
			const nextSourceIndex = ++ sourceIndex % this.state.svgSet.length
			return { sourceIndex : nextSourceIndex}
		}, function(){
			this.timeout = setTimeout(
				this.changeSource,
				this.props.animDuration * 100
			)
		})
	}
				// <img src={this.state.charSet[this.state.sourceIndex]} alt={"char-"+this.props.char} />
				// {this.state.charSet[this.state.sourceIndex]}

	render() {
		// console.log(this.state.charSet[this.state.sourceIndex])
		return (
			<div className="char-wrapper">
				<img src={this.state.svgSet[this.state.sourceIndex]} alt="R" className="char-svg" />
			</div>
		);
	}
}

export default R;