import React, { Component } from 'react';

import a1 from './src/01.svg';
import a2 from './src/02.svg';
import a3 from './src/03.svg';
import a4 from './src/04.svg';
import a5 from './src/05.svg';

// import Aone from './src/Aone';
// import Atwo from './src/Atwo';

export class A extends Component {

	constructor(props) {
    super(props);

    this.changeSource = this.changeSource.bind(this)
  }


	state = {
		sourceIndex : 0,
		svgSet : [ a1, a2, a3, a4, a5 ],
		// svgSet : [ Aone, Atwo ],
		svgSize: 100
	}

	componentDidMount () {
		console.log('componentDidMount:', this.props.svgPercent)

		// if (this.props.charCount < 3 ){
		// 	console.log('less than 3')
		// 	this.setState({svgHeight: 60})
		// } else if (this.props.charCount < 8 ){
		// 	this.setState({svgHeight: 50})
		// } else {
		// 	this.setState({svgHeight: 40})
		// }

		// console.log('svgHeight:', this.state.svgHeight)

		this.setState({char: this.props.char, svgSize: this.props.svgPercent});



	 if (this.props.loop ){

	 	this.timeout = setTimeout (
	 		this.changeSource,
	 		this.props.animDuration * 10
	 		)
	 }

	}

	componentWillMount () {
	  // clearInterval(this.interval);
	  if (this.timeout) clearTimeout(this.timeout)

	  // pull random character set from this.props.char 
	  // feed state 	
	}

	changeSource () {
		this.setState(function({sourceIndex}) {
			const nextSourceIndex = ++ sourceIndex % this.state.svgSet.length
			return { sourceIndex : nextSourceIndex}
		}, function(){
			this.timeout = setTimeout(
				this.changeSource,
				this.props.animDuration * 100
			)
		})
	}
				// <img src={this.state.charSet[this.state.sourceIndex]} alt={"char-"+this.props.char} />
				// {this.state.charSet[this.state.sourceIndex]}

	render() {
		// console.log(this.state.svgHeight)
		const pStyle = {
			// height: this.state.svgSize+'%'
		  // width: this.state.svgSize+'px'
		};

				// {this.state.svgSet[this.state.sourceIndex]}
		return (
			<div className={"char-wrapper"}>
				<img src={this.state.svgSet[this.state.sourceIndex]} alt="A" className="char-svg" style={pStyle}/>
			</div>
		);
	}
}

export default A;